import { 
	GET_ABOUT, 

} from "actions/const";

function reducer(state = null, action) {
  //console.log(action);
  const nextState = Object.assign({}, state);
  switch (action.type) {
    case GET_ABOUT:
      nextState.aboutPage = action.payload;
      return nextState;
 
    default:
  }
  return state;
}


export default reducer;